.item {
  outline: auto;
  outline-color: black;
}

.linkButton {
  color: white;
  text-decoration: none !important;
}

.placeholder {
  max-width: 190px;
  width: 100%;
  height: auto;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url('https://upload.wikimedia.org/wikipedia/commons/c/c2/No_image_poster.png');
}

.img-resp {
  max-width: 190px;
  width: 100%;
  height: auto;
}

.imdb {
  background-color: #f3ce13;
  color: black;
}

.scores, .station {
  width: 100%;
}

@media (width < 576px) {
  .item {
    min-width: 100px;
    width: 30%;
  }
  .image-text {
    font-size:x-small
  }
}

@media (576px < width <= 768px) {
  .item {
    width: 30%;
  }
}

@media (768px < width <= 992px) {
  .item {
    background-color: white
  }
}

@media (width > 992px) {
  .item {
    background-color: white;
  }
}